.p-drawer-menu {
	width: 100%;
	height: 100vh;
	background-color: rgba($color: #222, $alpha: 0.95);

	z-index: 100;
	text-align: center;
	display: none;
	max-width: rem(520);
	margin-left: auto;
}

.p-drawer-menu__logo {
	padding-top: rem(180);
}

.p-drawer-menu__items {
	margin-top: rem(20);
}

.p-drawer-menu__item a {
	font-family: $font-family-sub;
	font-weight: 800;
	text-transform: uppercase;
	font-size: rem(20);
	color: #fff;
	display: block;
	width: 100%;
	letter-spacing: 0.068;
	text-decoration: none;
	padding: rem(15) 0;
	line-height: 1;
}
