.p-box1 {

}

.p-box1__img {
  margin-top: rem(30);
  width: 100%;
  position: relative;
}

.p-box1__img::before {
  padding-top: 67.2463%; //  (232/345)
  display: block;
  content: "";
}

.p-box1__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  width: 100%;
  height: 100%;
  object-position: center;
}

.p-box1__body,
.p-box1__btn {
  margin-top: rem(30);
}